
  @import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&family=Karla:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400&family=Poppins:ital,wght@0,400;0,500;0,600;0,800;1,300&display=swap');
body{
  width: 1005;
  height: 100vh;
  font-family: 'Karla', sans-serif;
  background-color: #F5F7FB;
  margin: 0;
}
#root{
  width: 100%;
  height: 100%;
}
.App{
  width: 100%;
  height: 100%;
  background-color: #F5F7FB;
  position: relative;
}


.Quiz-container{
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  gap: 10px;
  justify-content: center;
  align-items:center;
}
.Quiz-title{
  margin: 0;
  font-size: 31.25px;
  font-weight: bold;
}
.Quiz-description{
  margin: 0;
  font-size: 16px;

}
.button-genric{
  background-color:#4D5B9E;
  padding: 26px;
  padding-right: 95px;
  padding-left: 95px;
  border-radius: 15px;
  margin-top: 20px;
  border: none;
  color: #F5F7FB;
  font-size: 16px;
  font-weight: medium;
}
.square-1-top{
  position: absolute;
  top: 0;
  left: 87.4%;
}
.square-2-bottom{
  position: absolute;
  bottom: 0;
  right: 87.4%;
}

.main-quiz-section{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.main-quiz{
  border-bottom: 1px solid #DBDEF0;
  width: 135%;
  max-width: 500px;
}

.main-quiz{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}
.container{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin: 0;
}
.Quizs{
  overflow: scroll;
  scrollbar-width: none;
  /* width: max-content; */
}
.quiz{
  color: #293264;
  font-family: 'karla','san-sarif';
  font-weight: bold;
  font-size: 20px;
}


.options-container{
  display: flex;
  gap: 15px;
  justify-content: flex-start;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 20px;
}
.options{
  box-sizing: border-box;
  border: 1px solid #4D5B9E;
  border-radius: 7.94px;
  padding: 0 15px;
  width: max-content;
  height: 25.65px;
  max-width: 190px;
  text-overflow: clip;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: transparent;
  cursor: pointer;
}

.options-text{
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  font-size: 10.24px;
  color: #4D5B9E;
}

.button-check-answer{
  padding: 17.5px 60px;
  font-family: 'karla','san-sarif';
  font-weight: 700;
  font-style: normal;

}

.selected{
  background-color: #D6DBF5;
  border: none;
  color: #293264;
  border: 1px solid #D6DBF5;
}
.wrong{
  background-color: #F8BCBC;
  border: none;
  color: #293264;
  border: 1px solid #F8BCBC;



}
.correct{
  background-color: #94D7A2;
  border: none;
  color: #293264;
  border: 1px solid #94D7A2;

}

.checkAnswer{
  display: flex;
  gap: 20px;
  justify-content:center;
  align-items: center;
  text-align: center;
}
.result{
  margin-bottom: 0;
  color: #293264;
  font-style: normal;
  font-weight: 700;
  font-size: 16.8px;
}